body {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  font-size: 20px;
  background-color: var(--color-bg);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1090px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1400px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

/* Components */
.header {
  background-color: var(--color-white);
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 15px 7px;
}

.header__logo {
  display: inline-block;
  width: 80px;
  height: 23px;
}

.header__logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-main);
}

.header__signin--mobile {
  width: var(--burger-width);
  height: var(--burger-height);
  background: url("../img/sprite.svg#signin") center/cover no-repeat;
}

@media (max-width: 1090px) {
  .header__btn {
    display: none;
  }
}

.header__start {
  border-radius: 4px;
  line-height: 25px;
  color: var(--color-white);
  background: var(--color-main);
  margin-left: 26px;
  padding: 13px 50px;
}

@media (max-width: 568px) {
  .header__start {
    width: 100%;
  }
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 105px;
  color: var(--color-header-text);
}

.menu__item:not(:last-child) {
  margin-right: 72px;
}

@media (max-width: 1090px) {
  .menu {
    display: none;
  }
}

.burger {
  position: relative;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}

.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger::before {
  top: 0;
}

.burger::after {
  top: calc(100% - var(--burger-line-height));
}

.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

